@use '../../scss/' as *;

.hero-slide__item {
    padding: 9rem 0;
    width: 100%;
    // height: 100vh;
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        @include overlay();
    }

    &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100px;
        background-image: linear-gradient(to top, $body-bg, rgba($black, 0));
    }

    &__content {
        @include flex(center, center);
        position: relative;

        &__info {
            width: 200%;
            padding: 0 3rem;
            position: relative;

            @include tablet {
                width: 100%;
            }

            &>*~* {
                margin-top: 3rem;
            }

            .title {
                font-size: 5rem;
                font-weight: 700;
                line-height: 1;

                @include tablet {
                    font-size: 4rem;
                }
            }

            .overview {
                font-weight: 600;
            }

            .btns>*~* {
                margin-left: 1rem;
            }

            .btns,
            .title,
            .overview {
                opacity: 0;
                transform: translateY(-100px);
                transition: transform .5s ease, opacity .5s ease;
            }
        }

        &__poster {
            // flex: 1;
            @include flex(center, flex-start);
            position: relative;

            img {
                width: 60%;
                border-radius: $border-radius;
                box-shadow: $box-shadow;

                transform: scale(0);
                transition: transform 1.7s ease;
            }

            @include tablet {
                display: none;
            }
        }
    }

    &.active>&__content>&__content__poster {
        img {
            transform: scale(1);
        }
    }

    &.active>&__content>&__content__info {

        .btns,
        .title,
        .overview {
            opacity: 1;
            transform: translateY(0);
        }

        .title {
            transition-delay: .3s, .3s;
        }

        .overview {
            transition-delay: .6s, .6s;
        }

        .btns {
            transition-delay: .9s, .9s;
        }
    }
}

hero-slide {
    margin-bottom: 3rem;
}